import React, { useState, useEffect } from "react";
import "./EditCostForm.css";
import { t } from "./localization";

function EditIncomeForm({ income, onSave, onCancel, language }) { //  props
  const [enteredType, setEnteredType] = useState("");
  const [enteredAmount, setEnteredAmount] = useState("");
  const [enteredDate, setEnteredDate] = useState("");

  useEffect(() => {
    if (income) {
      setEnteredType(income.type || "");
      setEnteredAmount(income.amount || "");
      setEnteredDate(
        income.date
          ? new Date(income.date).toISOString().split("T")[0]
          : ""
      );
    }
  }, [income]);

  const typeChangeHandler = (event) => {
    setEnteredType(event.target.value);
  };

  const amountChangeHandler = (event) => {
    setEnteredAmount(event.target.value);
  };

  const dateChangeHandler = (event) => {
    setEnteredDate(event.target.value);
  };

  const submitHandler = (event) => {
    event.preventDefault();

    if (!income.id) {
      console.error("Missing income ID for update");
      return;
    }

    const updatedIncomeData = {
      id: income.id,
      type: enteredType,
      amount: parseFloat(enteredAmount),
      date: enteredDate ? new Date(enteredDate) : null,
      entrytype: income.entrytype,
    };

    onSave(updatedIncomeData);
  };

  return (
  <form className="edit-cost-form" onSubmit={submitHandler}>
    <div className="edit-cost-form__controls">
      <div className="edit-cost-form__control">
        <label>{t("incomeType", language)}</label>
        <input type="text" value={enteredType} onChange={typeChangeHandler} />
      </div>
      <div className="edit-cost-form__control">
        <label>{t("amount", language)}</label>
        <input
          type="number"
          min="0.01"
          step="0.01"
          value={enteredAmount}
          onChange={amountChangeHandler}
        />
      </div>
      <div className="edit-cost-form__control">
        <label>{t("date", language)}</label>
        <input
          type="date"
          value={enteredDate}
          onChange={dateChangeHandler}
        />
      </div>
      <div className="edit-cost-form__control"></div>
    </div>
    <div className="edit-cost-form__actions">
      <button type="submit">{t("save", language)}</button>
      <button type="button" onClick={onCancel}>
        {t("cancel", language)}
      </button>
    </div>
  </form>
);

}

export default EditIncomeForm;
