import React, { useState } from "react";
import IncomeForm from "./IncomeForm";
import { t } from "../localization"; //   
import "./NewIncome.css";

const NewIncome = (props) => {
  const [isFormVisible, setIsFormVisible] = useState(false);

  const saveIncomeDataHandler = (inputIncomeData) => {
    const incomeData = {
      ...inputIncomeData,
      id: Math.random().toString(),
    };

    props.onAddIncome(incomeData); //   
    setIsFormVisible(false);
  };

  const showFormHandler = () => {
    setIsFormVisible(true);
  };

  const cancelFormHandler = () => {
    setIsFormVisible(false);
  };

  return (
    <div className="new-income">
      {!isFormVisible && (
        <button onClick={showFormHandler}>
          {t("addIncome", props.language)} {/*     */}
        </button>
      )}
      {isFormVisible && (
        <IncomeForm
          onSaveIncomeData={saveIncomeDataHandler}
          onCancel={cancelFormHandler}
          language={props.language} //   
        />
      )}
    </div>
  );
};

export default NewIncome;
