import React from "react";
import IncomeItem from "./IncomeItem";
import "./CostList.css";

const PiggyBankList = (props) => {
  if (props.entries.length === 0) {
    return <h2 className="cost-list__fallback">No piggy bank entries available</h2>;
  }

  return (
    <ul className="cost-list">
      {props.entries.map((entry) => (
        <IncomeItem
          key={entry.id}
          id={entry.id}
          date={entry.date}
          type={entry.type}
          amount={entry.amount}
          entrytype={entry.entrytype} //   
          onDelete={props.onDeleteEntry} //   
          onEdit={props.onEditEntry}
          language={props.language} //   
        />
      ))}
    </ul>
  );
};

export default PiggyBankList;
