import Diagram from './Diagramm/Diagram';
import { t } from "./localization";

const CostDiagram = (props) => {
  const monthLabels = [
    t("jan", props.language),
    t("feb", props.language),
    t("mar", props.language),
    t("apr", props.language),
    t("may", props.language),
    t("jun", props.language),
    t("jul", props.language),
    t("aug", props.language),
    t("sep", props.language),
    t("oct", props.language),
    t("nov", props.language),
    t("dec", props.language),
  ];

  const diagramDataSet = monthLabels.map((label) => ({
    label,
    value: 0,
    income: 0,
  }));

  for (const cost of props.costs) {
    const costMonth = cost.date.getMonth(); //  (0-11)
    diagramDataSet[costMonth].value += cost.price;
  }

  for (const income of props.incomes) {
    const incomeMonth = new Date(income.date).getMonth();
    diagramDataSet[incomeMonth].income += income.amount;
    diagramDataSet[incomeMonth].entrytype = income.entrytype;
  }

  return <Diagram dataSet={diagramDataSet} language={props.language} />;
};

export default CostDiagram;
