import React, { useState, useEffect } from "react";
import NewCost from "./components/NewCost/NewCost";
import NewIncome from "./components/NewCost/NewIncome";
import ItemList from "./components/ItemList";
import { supabase } from "./supabaseClient";
import Auth from "./components/Auth";
import Header from "./components/Header";
import IncomeList from "./components/IncomeList";
import EditIncomeForm from "./components/EditIncomeForm";
import PiggyBankList from "./components/PiggyBankList";
import { t } from './components/localization.js'


function App() {
  const [costs, setCosts] = useState([]);
  const [incomes, setIncomes] = useState([]);
  const [piggyBankEntries, setPiggyBankEntries] = useState([]);
  const [user, setUser] = useState(null);
  const [editingIncome, setEditingIncome] = useState(null);
   const [language, setLanguage] = useState("en");

 useEffect(() => {
  const initializeUser = async () => {
    try {
      const { data: sessionData, error } = await supabase.auth.getSession();
      if (error) {
        console.error("Error fetching session:", error);
        return;
      }

      const currentUser = sessionData?.session?.user || null;
      setUser(currentUser);

      if (currentUser) {
        const savedLanguage = await fetchUserLanguage(currentUser.id); //    
        if (savedLanguage) {
          setLanguage(savedLanguage);
        }
        fetchCosts(currentUser.id);
        fetchIncomes(currentUser.id);
        fetchPiggyBankEntries(currentUser.id);
      }

      const { data: subscription } = supabase.auth.onAuthStateChange(
        (event, session) => {
          const updatedUser = session?.user || null;
          setUser(updatedUser);

          if (updatedUser) {
            fetchCosts(updatedUser.id);
            fetchIncomes(updatedUser.id);
            fetchPiggyBankEntries(updatedUser.id);
          } else {
            setCosts([]);
            setIncomes([]);
            setPiggyBankEntries([]);
          }
        }
      );

      return () => {
        subscription?.unsubscribe();
      };
    } catch (error) {
      console.error("Error initializing user:", error);
    }
  };

  initializeUser(); //   
}, []);
 const fetchUserLanguage = async (userId) => {
    try {
      const { data, error } = await supabase
        .from("user_settings")
        .select("language")
        .eq("user_id", userId)
        .single();

      if (error) {
        console.error("Error fetching user language:", error);
        return null;
      }

      return data.language;
    } catch (err) {
      console.error("Unexpected error fetching language:", err);
      return null;
    }
  };

  //       
  const updateUserLanguage = async (userId, newLanguage) => {
    try {
      const { error } = await supabase
        .from("user_settings")
        .upsert({
          user_id: userId,
          language: newLanguage,
        });

      if (error) {
        console.error("Error updating user language:", error);
      }
    } catch (err) {
      console.error("Unexpected error updating language:", err);
    }
  };

  //  
  const handleLanguageChange = (newLanguage) => {
    setLanguage(newLanguage);

    if (user) {
      updateUserLanguage(user.id, newLanguage); //    
    }
  };
  const fetchCosts = async (userId) => {
    try {
      const { data, error } = await supabase
        .from("costs")
        .select("*")
        .eq("user_id", userId);

      if (error) {
        console.error("Error fetching costs:", error);
        return;
      }

      const transformedData = data.map((item) => ({
        ...item,
        date: new Date(item.date),
      }));
      setCosts(transformedData);
    } catch (error) {
      console.error("Error in fetchCosts:", error);
    }
  };

  const fetchIncomes = async (userId) => {
    try {
      const { data, error } = await supabase
        .from("debet")
        .select("*")
        .eq("user_id", userId);

      if (error) {
        console.error("Error fetching incomes:", error);
        return;
      }

      const transformedData = data.map((item) => ({
        ...item,
        date: new Date(item.date),
      }));

      setIncomes(transformedData);
    } catch (error) {
      console.error("Error in fetchIncomes:", error);
    }
  };

  const fetchPiggyBankEntries = async (userId) => {
    try {
      const { data, error } = await supabase
        .from("piggy_bank_entries")
        .select("*")
        .eq("user_id", userId);

      if (error) {
        console.error("Error fetching piggy bank entries:", error);
        return;
      }

      const transformedData = data.map((item) => ({
        ...item,
        date: new Date(item.date),
      }));

      setPiggyBankEntries(transformedData);
    } catch (error) {
      console.error("Error in fetchPiggyBankEntries:", error);
    }
  };

  const addIncomeHandler = async (income) => {
    try {
      const tableName =
        income.entrytype === "piggy bank" ? "piggy_bank_entries" : "debet";

      const { error } = await supabase.from(tableName).insert({
        user_id: user.id,
        type: income.type,
        amount: income.amount,
        date: income.date ? new Date(income.date).toISOString() : null,
        entrytype: income.entrytype || "income",
      });

      if (error) {
        console.error(`Error adding income to ${tableName}:`, error);
        return;
      }

      if (income.entrytype === "piggy bank") {
        fetchPiggyBankEntries(user.id);
      } else {
        fetchIncomes(user.id);
      }
    } catch (error) {
      console.error("Error in addIncomeHandler:", error);
    }
  };

  const editIncomeHandler = (incomeId, entrytype = "income") => {
    const incomeToEdit =
      entrytype === "piggy bank"
        ? piggyBankEntries.find((entry) => entry.id === incomeId)
        : incomes.find((income) => income.id === incomeId);

    setEditingIncome({ ...incomeToEdit, entrytype });
  };

  const saveEditedIncomeHandler = async (updatedIncome) => {
    try {
      const tableName =
        updatedIncome.entrytype === "piggy bank" ? "piggy_bank_entries" : "debet";

      const { error } = await supabase
        .from(tableName)
        .update({
          type: updatedIncome.type,
          amount: updatedIncome.amount,
          date: updatedIncome.date.toISOString(),
          entrytype: updatedIncome.entrytype,
        })
        .eq("id", updatedIncome.id);

      if (error) {
        console.error(`Error updating income in ${tableName}:`, error);
        return;
      }

      if (updatedIncome.entrytype === "piggy bank") {
        setPiggyBankEntries((prevEntries) =>
          prevEntries.map((entry) =>
            entry.id === updatedIncome.id ? { ...entry, ...updatedIncome } : entry
          )
        );
      } else {
        setIncomes((prevIncomes) =>
          prevIncomes.map((income) =>
            income.id === updatedIncome.id ? { ...income, ...updatedIncome } : income
          )
        );
      }

      setEditingIncome(null);
    } catch (error) {
      console.error("Error in saveEditedIncomeHandler:", error);
    }
  };

  const deleteIncomeHandler = async (incomeId, entrytype = "income") => {
    try {
      const tableName =
        entrytype === "piggy bank" ? "piggy_bank_entries" : "debet";

      const { error } = await supabase.from(tableName).delete().eq("id", incomeId);

      if (error) {
        console.error(`Error deleting income from ${tableName}:`, error);
        return;
      }

      if (entrytype === "piggy bank") {
        setPiggyBankEntries((prevEntries) =>
          prevEntries.filter((entry) => entry.id !== incomeId)
        );
      } else {
        setIncomes((prevIncomes) =>
          prevIncomes.filter((income) => income.id !== incomeId)
        );
      }
    } catch (error) {
      console.error("Error in deleteIncomeHandler:", error);
    }
  };

  const handleLogout = async () => {
    try {
      const { error } = await supabase.auth.signOut();
      if (error) {
        console.warn("Sign-out error occurred, but clearing local state.");
      }
      setUser(null);
      setCosts([]);
      setIncomes([]);
    } catch (error) {
      console.error("Error in handleLogout:", error);
    }
  };

  const calculateSavings = () => {
  const currentMonth = new Date().getMonth();
  const filteredIncomes = incomes.filter(
    (income) =>
      new Date(income.date).getMonth() === currentMonth &&
      income.entrytype !== "piggy bank" //    ""
  );

  const filteredCosts = costs.filter(
    (cost) => new Date(cost.date).getMonth() === currentMonth
  );

  const totalIncome = filteredIncomes.reduce(
    (sum, income) => sum + income.amount,
    0
  );
  const totalCost = filteredCosts.reduce((sum, cost) => sum + cost.price, 0);

  return totalIncome - totalCost;
};

const addCostHandler = async (cost) => {
  try {
    const { error } = await supabase.from("costs").insert({
      user_id: user.id,
      description: cost.description,
      price: cost.price,
      date: cost.date.toISOString(),
      category: cost.category,
    });

    if (error) {
      console.error("Error adding cost:", error);
      return;
    }

    fetchCosts(user.id); //     
  } catch (error) {
    console.error("Error in addCostHandler:", error);
  }
};

const updateCostHandler = async (updatedCost) => {
  try {
    const { error } = await supabase
      .from("costs")
      .update({
        description: updatedCost.description,
        price: updatedCost.price,
        date: updatedCost.date.toISOString(),
        category: updatedCost.category,
      })
      .eq("id", updatedCost.id);

    if (error) {
      console.error("Error updating cost:", error);
      return;
    }

    setCosts((prevCosts) =>
      prevCosts.map((cost) =>
        cost.id === updatedCost.id ? { ...cost, ...updatedCost } : cost
      )
    );
  } catch (error) {
    console.error("Error in updateCostHandler:", error);
  }
};


const deleteCostHandler = async (costId) => {
  try {
    const { error } = await supabase.from("costs").delete().eq("id", costId);

    if (error) {
      console.error("Error deleting cost:", error);
      return;
    }

    fetchCosts(user.id); //     
  } catch (error) {
    console.error("Error in deleteCostHandler:", error);
  }
};
  
  return (
     <div>
      {user && <Header user={user} onLogout={handleLogout} language={language} onLanguageChange={handleLanguageChange} />}
      {!user && <Auth onAuthenticated={(user) => setUser(user)} language={language} onLanguageChange={handleLanguageChange}/>}
      {user && (
        <>
          <div className="savings-container">
            <h2>{t("savings", language)}: ${calculateSavings()}</h2>
          </div>
           <NewCost onAddCost={addCostHandler} language={language} />
          <NewIncome onAddIncome={addIncomeHandler} language={language} />

          {editingIncome && (
            <EditIncomeForm
              income={editingIncome}
              onSave={saveEditedIncomeHandler}
              onCancel={() => setEditingIncome(null)}
              language={language}
            />
          )}

         <div className="items-container">
            <ItemList
              costs={costs}
              incomes={incomes}
              onUpdateCost={updateCostHandler}
              onDeleteCost={deleteCostHandler}
              language={language}
            />
            <IncomeList
              incomes={incomes}
              onEditIncome={(id) => editIncomeHandler(id, "income")}
              onDeleteIncome={(id) => deleteIncomeHandler(id, "income")}
              language={language}
            />
            <PiggyBankList
              entries={piggyBankEntries}
              onEditEntry={(id) => editIncomeHandler(id, "piggy bank")}
              onDeleteEntry={(id) => deleteIncomeHandler(id, "piggy bank")}
              language={language}
            />
          </div>
        </>
      )}
    </div>
  );
}

export default App;
