import React from "react";
import CostItem from "./CostItem";
import "./CostList.css";

const CostList = ({ costs, onDeleteCost, onEdit, language }) => { //  `language`
  if (costs.length === 0) {
    return <h2 className="cost-list__fallback">No costs found.</h2>;
  }

  return (
    <ul className="cost-list">
      {costs.map((cost) => (
        <CostItem
          key={cost.id}
          id={cost.id}
          date={cost.date}
          description={cost.description}
          price={cost.price}
          category={cost.category}
          onDelete={onDeleteCost}
          onEdit={onEdit}
          language={language} //     CostItem
        />
      ))}
    </ul>
  );
};

export default CostList;
