import React, { useState, useEffect } from "react";
import "./EditCostForm.css";
import { t } from "./localization"; //   

function EditCostForm(props) {
  useEffect(() => {
    if (props.cost) {
      setEnteredDescription(props.cost.description || "");
      setEnteredPrice(props.cost.price || "");
      setEnteredDate(
        props.cost.date
          ? new Date(props.cost.date).toISOString().split("T")[0]
          : ""
      );
      setEnteredCategory(props.cost.category || "");
      setCustomCategory("");
    }
  }, [props.cost]);

  const [enteredDescription, setEnteredDescription] = useState("");
  const [enteredPrice, setEnteredPrice] = useState("");
  const [enteredDate, setEnteredDate] = useState("");
  const [enteredCategory, setEnteredCategory] = useState("");
  const [customCategory, setCustomCategory] = useState("");

  //  
  const categoryOptions = [
    t("food", props.language),
    t("transport", props.language),
    t("entertainment", props.language),
    t("health", props.language),
    t("education", props.language),
    t("other", props.language),
  ];

  const descriptionChangeHandler = (event) => {
    setEnteredDescription(event.target.value);
  };

  const priceChangeHandler = (event) => {
    setEnteredPrice(event.target.value);
  };

  const dateChangeHandler = (event) => {
    setEnteredDate(event.target.value);
  };

  const categoryChangeHandler = (event) => {
    setEnteredCategory(event.target.value);
    setCustomCategory("");
  };

  const customCategoryChangeHandler = (event) => {
    setCustomCategory(event.target.value);
    setEnteredCategory("");
  };

  const submitHandler = (event) => {
    event.preventDefault();

    if (!props.cost.id) {
      console.error("Missing cost ID for update");
      return;
    }

    const updatedCostData = {
      id: props.cost.id,
      description: enteredDescription,
      price: parseFloat(enteredPrice),
      date: enteredDate ? new Date(enteredDate) : null,
      category: customCategory || enteredCategory,
      entryType: props.cost.entryType,
    };

    props.onSave(updatedCostData);
  };

  return (
    <form className="edit-cost-form" onSubmit={submitHandler}>
      <div className="edit-cost-form__controls">
        <div className="edit-cost-form__control">
          <label>{t("description", props.language)}</label>
          <input
            type="text"
            value={enteredDescription}
            onChange={descriptionChangeHandler}
          />
        </div>
        <div className="edit-cost-form__control">
          <label>{t("price", props.language)}</label>
          <input
            type="number"
            min="0.01"
            step="0.01"
            value={enteredPrice}
            onChange={priceChangeHandler}
          />
        </div>
        <div className="edit-cost-form__control">
          <label>{t("date", props.language)}</label>
          <input
            type="date"
            value={enteredDate}
            onChange={dateChangeHandler}
          />
        </div>
        <div className="edit-cost-form__control">
          <label>{t("category", props.language)}</label>
          <select value={enteredCategory} onChange={categoryChangeHandler}>
            <option value="">{t("selectCategory", props.language)}</option>
            {categoryOptions.map((category, index) => (
              <option key={index} value={category}>
                {category}
              </option>
            ))}
          </select>
          <input
            type="text"
            placeholder={t("enterCustomCategory", props.language)}
            value={customCategory}
            onChange={customCategoryChangeHandler}
          />
        </div>
      </div>
      <div className="edit-cost-form__actions">
        <button type="submit">{t("save", props.language)}</button>
        <button type="button" onClick={props.onCancel}>
          {t("cancel", props.language)}
        </button>
      </div>
    </form>
  );
}

export default EditCostForm;
