import { useState } from "react";
import { supabase } from "../supabaseClient";
import { t } from "./localization"; //   
import "./Auth.css";

const Auth = ({ language, onAuthenticated, onLanguageChange }) => { //  onLanguageChange
  const [isOtpSent, setIsOtpSent] = useState(false);
  const [otpMode, setOtpMode] = useState(false); 
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [otp, setOtp] = useState("");
  const [errorMessage, setErrorMessage] = useState(""); 
  const [emailError, setEmailError] = useState(false);

  const handleSignUpWithOTP = async () => {
    try {
      setErrorMessage("");
      setEmailError(false);

      const { error } = await supabase.auth.signUp({
        email,
        password,
      });

      if (error) throw error;

      setIsOtpSent(true);
    } catch (error) {
      setErrorMessage(error.message);
    }
  };

  const handleVerifyOTP = async () => {
    try {
      setErrorMessage("");
      setEmailError(false);

      const { error } = await supabase.auth.verifyOtp({
        email,
        token: otp,
        type: "signup",
      });

      if (error) throw error;

      setIsOtpSent(false);
    } catch (error) {
      setErrorMessage(error.message);
    }
  };

  const handleSignIn = async () => {
    try {
      setErrorMessage("");
      setEmailError(false);

      const { data, error } = await supabase.auth.signInWithPassword({
        email,
        password,
      });

      if (error) throw error;
      onAuthenticated(data.user);
    } catch (error) {
      setErrorMessage(t("invalidLoginOrPassword", language));
      setEmailError(true);
    }
  };

  const resetError = () => {
    setErrorMessage("");
    setEmailError(false);
  };

  return (
    <div className="auth-container">
      <h2 className="auth-heading">{t("welcome", language)}</h2>

      {/*     */}
      <div className="language-selector">
  <button
    onClick={() => onLanguageChange("en")}
    className={`language-button ${language === "en" ? "active" : ""}`}
  >
    English
  </button>
  <button
    onClick={() => onLanguageChange("ru")}
    className={`language-button ${language === "ru" ? "active" : ""}`}
  >
    
  </button>
  <button
    onClick={() => onLanguageChange("uk")}
    className={`language-button ${language === "uk" ? "active" : ""}`}
  >
    
  </button>
</div>

      <div className="auth-form">
        <input
          type="email"
          placeholder={t("emailPlaceholder", language)}
          value={email}
          onChange={(e) => {
            setEmail(e.target.value);
            resetError();
          }}
          className={`auth-input ${emailError ? "error" : ""}`}
        />
        {!otpMode && (
          <input
            type="password"
            placeholder={t("passwordPlaceholder", language)}
            value={password}
            onChange={(e) => {
              setPassword(e.target.value);
              resetError();
            }}
            className="auth-input"
          />
        )}

        {isOtpSent && otpMode && (
          <input
            type="text"
            placeholder={t("enterOtpPlaceholder", language)}
            value={otp}
            onChange={(e) => {
              setOtp(e.target.value);
              resetError();
            }}
            className="auth-input"
          />
        )}

        {errorMessage && <div className="error-message">{errorMessage}</div>}

        <div className="auth-buttons">
          {!otpMode ? (
            <>
              <button onClick={handleSignIn} className="auth-button signin">
                {t("signInButton", language)}
              </button>
              <button
                onClick={() => {
                  setOtpMode(true);
                  resetError();
                }}
                className="auth-button signup"
              >
                {t("signUpWithOtpButton", language)}
              </button>
            </>
          ) : isOtpSent ? (
            <button onClick={handleVerifyOTP} className="auth-button verify-otp">
              {t("verifyOtpButton", language)}
            </button>
          ) : (
            <button onClick={handleSignUpWithOTP} className="auth-button signup">
              {t("sendOtpButton", language)}
            </button>
          )}
        </div>

        {otpMode && (
          <button
            onClick={() => {
              setOtpMode(false);
              resetError();
            }}
            className="auth-button back"
          >
            {t("backToSignInButton", language)}
          </button>
        )}
      </div>
    </div>
  );
};

export default Auth;
