
import React, { useState } from "react";
import Card from "./Card";
import CostsFilter from "./CostsFilter";
import CostList from "./CostList";
import CostDiagram from "./CostDiagram";
import EditCostForm from "./EditCostForm";
import PieChartComponent from "./PieChartComponent";
import { t } from "./localization";


function ItemList({ costs, incomes, onUpdateCost, onDeleteCost, language}) {
  const currentYear = new Date().getFullYear().toString();
  const [selectedYear, setSelectedYear] = useState(currentYear);
  const [editingCost, setEditingCost] = useState(null);

  const yearChangeHandler = (year) => {
    setSelectedYear(year);
  };

  const filteredCosts = costs.filter((cost) => {
    const costDate = new Date(cost.date);
    return costDate.getFullYear().toString() === selectedYear;
  });

  const editCostHandler = (costId) => {
    const costToEdit = costs.find((cost) => cost.id === costId);
    setEditingCost(costToEdit);
  };

  const saveEditedCostHandler = async (updatedCost) => {
    await onUpdateCost(updatedCost);
    setEditingCost(null); //     
  };

  const preparePieChartData = (costs, language) => {
  const categoryTotals = {};

  costs.forEach((cost) => {
    if (!cost.category) return;
    if (!categoryTotals[cost.category]) {
      categoryTotals[cost.category] = 0;
    }
    categoryTotals[cost.category] += cost.price;
  });

  //  
  return Object.entries(categoryTotals).map(([category, value]) => ({
    category: t(category, language), //  
    value,
  }));
};
  return (
    <div>
      {editingCost && (
        <EditCostForm
          cost={editingCost}
          onSave={saveEditedCostHandler}
          onCancel={() => setEditingCost(null)}
          language={language} //  
        />
      )}
      {!editingCost && (
        <Card className="costs">
          <CostsFilter
            year={selectedYear}
            onChangeYear={yearChangeHandler}
            language={language} //  
          />
           <PieChartComponent
  data={preparePieChartData(costs, language)} //     
  language={language} //   
/>
          <CostDiagram
            costs={filteredCosts}
            incomes={incomes}
            language={language} //  
          />
          <CostList
            costs={filteredCosts}
            onDeleteCost={onDeleteCost}
            onEdit={editCostHandler}
            language={language} //  
          />
        </Card>
      )}
    </div>
  );
}

export default ItemList;