import React, { useState } from "react";
import Bar from "./Bar";
import "./Diagram.css";
import { t } from "../localization"; //   

const Diagram = (props) => {
  const [showDifference, setShowDifference] = useState(false);

  //  ,      "piggy bank"
  const filteredDataSet = props.dataSet.map((data) => {
    if (data.income && data.entrytype === "piggy bank") {
      return {
        ...data,
        income: 0, //  ,   "piggy bank"
      };
    }
    return data; //     
  });

  //       
  const adjustedDataSet = filteredDataSet.map((data) => ({
    ...data,
    value: showDifference
      ? (data.income || 0) - data.value //    
      : data.value, //  
  }));

  //      
  const maxMonthCost = Math.max(1, ...adjustedDataSet.map((data) => data.value));

  return (
    <div className="diagram-container">
      <div className="diagram">
        {adjustedDataSet.map((data) => (
          <div key={data.label} className="diagram-bar-container">
            <Bar value={data.value} maxValue={maxMonthCost} label={data.label} />
            <div className="bar-cost">${data.value.toFixed(2)}</div>
          </div>
        ))}
      </div>
      <div className="diagram-toggle">
        <label className="toggle-switch">
          <input
            type="checkbox"
            checked={showDifference}
            onChange={() => setShowDifference((prev) => !prev)}
          />
          <span className="slider"></span>
        </label>
        <span className="toggle-label">
          {showDifference
            ? t("calculateWithIncome", props.language) 
            : t("calculateWithoutIncome", props.language)} 
        </span>
      </div>
    </div>
  );
};

export default Diagram;
