import React, { useState, useEffect } from "react";
import { supabase } from "../supabaseClient";
import "./Header.css";
import { t } from './localization.js'

const Header = ({ user, onLogout, language, onLanguageChange }) => {
  const [isModalOpen, setModalOpen] = useState(false);
  const [isChangePasswordVisible, setChangePasswordVisible] = useState(false);
  const [oldPassword, setOldPassword] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [error, setError] = useState("");
  const [success, setSuccess] = useState("");
   //    

 

  useEffect(() => {
    if (isModalOpen) {
      document.body.classList.add("modal-open");
    } else {
      document.body.classList.remove("modal-open");
    }
    return () => {
      document.body.classList.remove("modal-open");
    };
  }, [isModalOpen]);

  const toggleModal = () => {
    setModalOpen((prev) => !prev);
    setChangePasswordVisible(false);
    setOldPassword("");
    setNewPassword("");
    setError("");
    setSuccess("");
  };

  

  const handlePasswordChange = async () => {
    try {
      setError("");
      setSuccess("");

      if (oldPassword === newPassword) {
        setError("Old and new passwords must not be the same.");
        return;
      }

      const { error: signInError } = await supabase.auth.signInWithPassword({
        email: user.email,
        password: oldPassword,
      });

      if (signInError) {
        setError("Incorrect old password.");
        return;
      }

      const { error: updateError } = await supabase.auth.updateUser({
        password: newPassword,
      });

      if (updateError) {
        throw updateError;
      }

      setSuccess("Password updated successfully!");
      setOldPassword("");
      setNewPassword("");
      setChangePasswordVisible(false);
    } catch (err) {
      setError(err.message || "Failed to update password.");
    }
  };

  const currentDate = new Date().toLocaleDateString(language, {
    weekday: "long",
    month: "long",
    day: "numeric",
  });

  const userInitial = user?.email ? user.email.charAt(0).toUpperCase() : "G";
  const userEmail = user?.email || "Guest";

 return (
    <header className="app-header">
      <div className="header-content">
        <div className="header-left">
          <h1 className="app-title">
            Penny<span>Wise</span>
          </h1>
          <p className="header-date">{currentDate}</p>
        </div>
        <div className="header-right">
          <div
            className="user-avatar"
            title={userEmail}
            onClick={toggleModal}
            style={{ cursor: "pointer" }}
          >
            {userInitial}
          </div>
        </div>
      </div>
      {isModalOpen && (
        <div className="modal-backdrop" onClick={toggleModal}>
          <div className="modal" onClick={(e) => e.stopPropagation()}>
            <h3>{t("userOptions", language)}</h3>
            <div className="modal-content">
              {!isChangePasswordVisible && (
                <>
                  <div className="language-selector">
  <h4 className="language-title">{t("changeLanguage", language)}</h4>
  <div className="language-buttons">
    <button
      onClick={() => onLanguageChange("en")}
      className={`language-button ${language === "en" ? "active" : ""}`}
    >
      English
    </button>
    <button
      onClick={() => onLanguageChange("ru")}
      className={`language-button ${language === "ru" ? "active" : ""}`}
    >
      
    </button>
    <button
      onClick={() => onLanguageChange("uk")}
      className={`language-button ${language === "uk" ? "active" : ""}`}
    >
      
    </button>
  </div>
</div>
                  <button
                    onClick={() => setChangePasswordVisible(true)}
                    className="primary-button"
                  >
                    {t("changePassword", language)}
                  </button>
               
                </>
              )}
              {isChangePasswordVisible && (
                <>
                  <label>{t("oldPassword", language)}</label>
                  <input
                    type="password"
                    value={oldPassword}
                    onChange={(e) => setOldPassword(e.target.value)}
                  />
                  <label>{t("newPassword", language)}</label>
                  <input
                    type="password"
                    value={newPassword}
                    onChange={(e) => setNewPassword(e.target.value)}
                  />
                  <button
                    onClick={handlePasswordChange}
                    className="primary-button"
                  >
                    {t("savePassword", language)}
                  </button>
                </>
              )}
              {success && <p className="success-message">{success}</p>}
              {error && <p className="error-message">{error}</p>}
              <button onClick={onLogout} className="logout-button">
                {t("logout", language)}
              </button>
              <button onClick={toggleModal} className="close-button">
                {t("close", language)}
              </button>
            </div>
          </div>
        </div>
      )}
    </header>
  );
};

export default Header;