import React from "react";
import IncomeItem from "./IncomeItem";
import "./IncomeList.css";

const IncomeList = ({ incomes, onDeleteIncome, onEditIncome, language }) => {
  if (incomes.length === 0) {
    return <h2 className="income-list__fallback">No incomes available</h2>;
  }

  return (
    <ul className="income-list">
      {incomes.map((income) => (
        <IncomeItem
          key={income.id}
          id={income.id}
          date={income.date}
          type={income.type}
          amount={income.amount}
          entrytype={income.entrytype}
          onDelete={onDeleteIncome}
          onEdit={onEditIncome}
          language={language} //  
        />
      ))}
    </ul>
  );
};

export default IncomeList;
