import { useState } from "react";
import "./CostForm.css";
import { t } from "../localization";

const CostForm = (props) => {
  const [description, setDescription] = useState("");
  const [price, setPrice] = useState("");
  const [date, setDate] = useState("");
  const [category, setCategory] = useState("");
  const [error, setError] = useState(""); //   

  //       
  const popularCategories = [
    "food",
    "transport",
    "entertainment",
    "utilities",
  ];

  const submitHandler = (event) => {
    event.preventDefault();

    // :    
    if (!date) {
      setError(t("selectValidDate", props.language)); //  
      return;
    }

    if (price <= 0) {
      setError(t("priceGreaterThanZero", props.language)); //  
      return;
    }

    if (!category.trim()) {
      setError(t("selectOrEnterCategory", props.language)); //  
      return;
    }

    const costData = {
      description,
      price: parseFloat(price),
      date: new Date(date),
      category,
    };

    //     
    props.onSaveCostData(costData);

    //     
    setDescription("");
    setPrice("");
    setDate("");
    setCategory("");
    setError("");
  };

  return (
    <form onSubmit={submitHandler}>
      <div className="new-cost__controls">
        <div className="new-cost__control">
          <label>{t("name", props.language)}</label>
          <input
            type="text"
            value={description}
            onChange={(e) => setDescription(e.target.value)}
          />
        </div>
        <div className="new-cost__control">
          <label>{t("price", props.language)}</label>
          <input
            type="number"
            min="0.01"
            step="0.01"
            value={price}
            onChange={(e) => setPrice(e.target.value)}
          />
        </div>
        <div className="new-cost__control">
          <label>{t("date", props.language)}</label>
          <input
            type="date"
            min="2022-01-01"
            max="2025-12-31"
            value={date}
            onChange={(e) => setDate(e.target.value)}
          />
        </div>
        <div className="new-cost__control">
          <label>{t("category", props.language)}</label>
          <input
            type="text"
            placeholder={t("enterCategory", props.language)}
            value={category}
            onChange={(e) => setCategory(e.target.value)}
          />
          <select
            value={category}
            onChange={(e) => setCategory(e.target.value)}
          >
            <option value="">{t("selectCategory", props.language)}</option>
            {popularCategories.map((cat) => (
              <option key={cat} value={cat}>
                {t(cat, props.language)}
              </option>
            ))}
          </select>
        </div>
        {error && <p className="error-message">{error}</p>}
        <div className="new-cost__actions">
          <button type="submit">{t("addCost", props.language)}</button>
          <button type="button" onClick={props.onCancel}>
            {t("cancel", props.language)}
          </button>
        </div>
      </div>
    </form>
  );
};

export default CostForm;
